import React, { useState } from "react";

import Header from "./components/Header";
import TodoList from "./components/TodoList";

function App() {
  const [todos, setTodos] = useState([
    {
      id: 1,
      title: "Learn React",
      description: "Learn the basic of JavaScript library"
    },
    {
      id: 2,
      title: "Styled Components",
      description: "Learn the styled components for css on react"
    },
    {
      id: 3,
      title: "Learn Redux",
      description: "Learn manage the state con react on the store"
    }
  ]);
  const [addTodos, setAddTodos] = useState({})
  const handleChange = ({ target }) => {
    const { name, value } = target;
    console.log(addTodos)
    setAddTodos({
      ...addTodos,
      id: todos.length + 1,
      [name]: value,
    })
  };

  const handleSubmit = event => {
    event.preventDefault();

    if (addTodos.title === '' || addTodos.description === '') {
      return;
    } else {
      console.log(addTodos);
      setTodos([...todos, addTodos]);
      setAddTodos({
        title: "",
        description: ""
      });
    }
  }

  return (
    <React.Fragment>
      <Header title={"Todo App - ReactJs"} />
      <TodoList
        todos={todos}
        handleChange={handleChange}
        handleSubmit={handleSubmit}
        addTodos={addTodos}
      />
    </React.Fragment>
  );
}

export default App;
